  import Scroll from "../../base/scroll/scroll.vue"
  import Step from "../../base/step/step.vue"
  import Upload from "../../base/upload/upload.vue"

  export default {
    name: "getPrice1",
    components: {
      Step,
      Upload,
      Scroll
    },
    data() {
      return {
        stepArr: ["文字好评", "收藏商品"],
        stepIndex: 0,
        isOk: true, //按钮可点击
        twoInfo:{},
        text:''
      }
    },
    //    接口请求部分开始
    created(){
      let that = this;
//      核对商品的詳情接口
      this.$axios.post('/api/orderOperate/getAdditionalInfo', {
        buyerTaskRecordId: that.$route.query.buyerTaskRecordId
      }).then((res) => {
        let data = res.data
        if (data.code=== "200") {
          this.twoInfo=data.data
        }
      }).catch((error) => {
        this.$vux.alert.show({
          title:'错误提示',
          content: '服务器错误',
        })
      })
    },
//    接口请求部分结束
    methods: {
      next(){
        console.log(this.text)
        if (this.text!=='') {
          let that = this;
          this.$axios.post('/api/orderOperate/backOrderSubmit', {
            buyerTaskRecordId: that.$route.query.buyerTaskRecordId,
            text:this.text
          }).then((res) => {
            console.log(res.data.code)
            if (res.data.code == 200) {
              this.$router.push({ name: 'submitSuccess',query:{type:'evaluate4'}})
            }
          })
        }else{
          this.$vux.alert.show({
            title:'错误提示',
            content: '内容不能为空',
          })
        }
      }

    }
  }



